@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}
.container{
    position: relative;
    max-width: 1350px;
    width: 100%;
    padding: 20px;
    margin: 0px auto;
}
.filter_buttons {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
}
button {
    padding: 10px 20px;
    font-size: 18px;
    background: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}
button.active {
    background: #4070f4;
    color: #fff;
}
.filterable_cards {
    display: flex;
    margin-top: 25px;
    gap: 20px;
    flex-wrap: wrap;
}
.card {
    flex-grow: 1;
    flex-basis: 40%;
    background-color: #fff;
    padding: 3px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}
.card.hide {
    display: none;
}
.card img {
    width: 100%;
    height: 210px;
    object-fit: cover;
    border-radius: 8px 8px 0 0;
}
.card_body {
    padding: 15px 20px 20px;
}
.card_title {
    font-size: 20px;
    font-weight: 500;
    color: #333;
}.card_text {
    font-size: 18px;
    color: #333;
}